.input-field {
    background: #fff;
    font-weight: 300;
    margin: 5px;
    padding: 10px;
    width: 280px;
    border: 1px solid #000;
    border-radius: 5px;
    outline: none;
}

#forgot-pass {
    border-bottom: 1px solid #ccc;
    color: #000;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 0.3px;
    padding: 3px;
    text-decoration: none;

}

.submit-btn {
    border: none;
    border-radius: 25px;
    color: #fff;
    cursor: pointer;
    font-size: 11px;
    letter-spacing: 1px;
    margin-top: 20px;
    padding: 15px 50px;
    text-transform: uppercase;
    outline: none;
}

.login .submit-btn {
    background-image: linear-gradient(135deg, #07a81d 30%, #054211 90%);
}

.login .submit-btn:hover {
    background: linear-gradient(to right, #054211 0, #07a81d 100%);
}

.register .submit-btn {
    background-image: linear-gradient(135deg, #07a81d 30%, #054211 90%);
}

.register .submit-btn:hover {
    background: linear-gradient(to right, #054211 0, #07a81d 100%);
}