.liveStream {
    width: 100%;
    height: 100%;
    background-color: #0B1416;
    display: flex;
}


.liveStream-liveBox {
    width: 100%;
    background-color: transparent;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.liveStream-message {
    width: 35%;
    height: 90vh;
    position: relative;
    background-color: #212529;
    /* display: none; */
}

.messageBox {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.messageBox input {
    outline: none;
    border-radius: 40px;
    background-color: #1A282D;
    padding: 10px;
    color: #fff;
    width: 85%;
    border: none
}

